import openWindow from '@/utils/openWindow';
export default {
  name: 'social-signin',
  methods: {
    wechatHandleClick: function wechatHandleClick(thirdpart) {
      this.$store.commit('SET_AUTH_TYPE', thirdpart);
      var appid = 'xxxxx';
      var redirectUri = encodeURIComponent('xxx/redirect?redirect=' + window.location.origin + '/authredirect');
      var url = 'https://open.weixin.qq.com/connect/qrconnect?appid=' + appid + '&redirect_uri=' + redirectUri + '&response_type=code&scope=snsapi_login#wechat_redirect';
      openWindow(url, thirdpart, 540, 540);
    },
    tencentHandleClick: function tencentHandleClick(thirdpart) {
      this.$store.commit('SET_AUTH_TYPE', thirdpart);
      var clientId = 'xxxxx';
      var redirectUri = encodeURIComponent('xxx/redirect?redirect=' + window.location.origin + '/authredirect');
      var url = 'https://graph.qq.com/oauth2.0/authorize?response_type=code&client_id=' + clientId + '&redirect_uri=' + redirectUri;
      openWindow(url, thirdpart, 540, 540);
    }
  }
};